<script setup>
import {defineProps, defineEmits, watch} from 'vue';

const props = defineProps({
  headers: {
    type: Array,
    default: () => [
      {
        text: 'Dessert (100g serving)',
        align: 'start',
        sortable: false,
        value: 'name',
      },
      {text: 'Calories', value: 'calories'},
      {text: 'Fat (g)', value: 'fat'},
      {text: 'Carbs (g)', value: 'carbs'},
      {text: 'Protein (g)', value: 'protein'},
      {text: 'Iron (%)', value: 'iron'},
    ],
  },
  data: {
    type: Array,
  },
  loading: {
    type: Boolean,
  },
  rowsClass: {
    type: String,
  },
  tableClass: {
    type: String,
  },
  class: {
    type: String,
  },
  headerClass: {
    type: String,
  },
  isCheckbox: {
    type: Boolean,
    default: () => false,
  },
  indicator: {
    type: Boolean,
    default: () => false,
  },
  classBody: {
    type: String,
  },
  blured: {
    type: String,
  },
  reRender: {
    type: String,
  },
});
const $emit = defineEmits(['click', 'check-header']);

watch(
  () => props.reRender,
  (val) => {
    const inp = document.querySelectorAll(`input[type="checkbox"]`);
    inp.forEach((el) => {
      el.checked = false;
    });
  }
);

const headerValue = (item, header) => {
  if (Array.isArray(header.value)) {
    return item[header.value[0]][header.value[1]];
  } else {
    return item[header.value];
  }
};
</script>
<template>
  <div class="data-table" :class="props.class">
    <table class="w-full">
      <thead>
        <tr>
          <th class="w-28 text-left text-xs px-0.5">T/r</th>
          <th class="text-left text-xs px-0.5" v-for="header in headers" :key="header.value" :class="[header.class, header.value]">
            {{ header.text }}
          </th>
        </tr>
      </thead>
      <tbody :class="[classBody, {ischeckbox: isCheckbox}]">
        <tr :class="[rowsClass, item[blured] ? 'blured' : '']" v-for="(item, index) in data" @click="$emit('click', item)" :key="index">
          <td class="w-28 px-0.5">{{ index + 1 }}</td>
          <td v-for="header in headers" :key="header.value" :title="headerValue(item, header)" :class="[header.class, header.value, ' px-0.5']">
            <slot :name="header.value" :items="{index, item, text: headerValue(item, header), value: header.value}">
              <span class="text-xs">{{ headerValue(item, header) }}</span>
            </slot>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<style scoped>
table {
  border-collapse: collapse;
}
table,
th,
td {
  border: 1px solid;
}
</style>
