import axios from './api/axios';

const JuvenilesService = {
  async fetchJuvenileById(id) {
    const {data} = await axios.get(`/api/juvenile/juveniles/${id}/`);
    return data;
  },
  async fetchJuvenileInfoDetailById(id) {
    const {data} = await axios.get(`/api/juvenile/juveniles/${id}/juvenile_info_detail/`);
    return data;
  },
  async fetchJuvenilesList(params = {}) {
    return (
      await axios.get('/api/juvenile/juveniles/', {
        params,
      })
    ).data;
  },
  async fetchJuvenileReportsList(params = {}) {
    return (
      await axios.get('/api/juvenile/reports/', {
        params: {
          ...params,
        },
      })
    ).data;
  },
  async fetchJuvenileReportById(id) {
    const {data} = await axios.get(`/api/juvenile/is_filled/${id}/`);
    return data;
  },
  async fetchJuvenileReportsInfoById(id) {
    const {data} = await axios.get(`/api/juvenile/reports/${id}`);
    return data;
  },
  async fetchAcceptedJuvenilesInfo(id) {
    const {data} = await axios.get(`/api/juvenile/accepted_for_edit/${id}/`);
    return data;
  },
  async acceptJuvenileToCenter(id, formData) {
    return await axios.post(`/api/juvenile/juveniles/accept_juvenile/`, formData, {
      params: {
        juvenile_id: id,
      },
    });
  },
  async createAvailableJuvenile(juvenileId) {
    return await axios.post(`/api/juvenile/juveniles/create_available_juvenile/?juvenile_id=${juvenileId}`);
  },
  async updateJuvenile(formData, id) {
    return await axios.put('/api/juvenile/juveniles/' + id, formData);
  },
  async createJuvenilePersonalInfo(formData) {
    return await axios.post('/api/juvenile/juveniles/', formData);
  },
  async createJuvenileAddressInfo(id, formData) {
    return await axios.post(`/api/juvenile/juveniles/${id}/juvenile_addressinfo_create_or_update/`, formData);
  },
  async createJuvenileEducationInfo(id, formData) {
    return await axios.post(`/api/juvenile/juveniles/${id}/juvenile_educationinfo_create_or_update/`, formData);
  },
  async getDeterminedJuvenilesList(params) {
    const response = (
      await axios.get('/api/juvenile/juveniles/incomplete_juveniles/', {
        params,
      })
    ).data;
    const normalizedResultsList = response.results.map((item) => ({
      birth_date: item.personal_info.birth_date,
      father_name: item.personal_info.father_name,
      first_name: item.personal_info.first_name,
      last_name: item.personal_info.last_name,
      ...item,
    }));
    return {
      ...response,
      results: normalizedResultsList,
    };
  },
  async getNotDefinedJuvenilesList(params) {
    return (
      await axios.get('/api/juvenile/juveniles/unidentified_juveniles', {
        params,
      })
    ).data;
  },
};
export default JuvenilesService;
