const route = useRoute();

const teenagerInfo = ref(null);

const teenParents = reactive({
  isNotEmpty: false,
  father: {},
  mother: {},
});

const loading = ref(true);

const {alertThisError} = useErrorAlert();

const {fetchJuvenileReportsInfoById} = JuvenilesService;

const fetchTeenager = async () => {
  try {
    teenagerInfo.value = await fetchJuvenileReportsInfoById(route.params.id);

    teenagerInfo.value.parent_info &&
      (teenagerInfo.value.parent_info.parents = teenagerInfo.value?.parent_info.parents.map((n) => {
        let item = {
          full_name: `${n?.first_name || ''} ${n?.last_name || ''} ${n?.father_name || ''}`,
          birthDate: new Date(n?.birth_date).toLocaleDateString('ru-Ru', {
            year: 'numeric',
            month: 'numeric',
            day: 'numeric',
          }),
          isAbroad: !n?.is_abroad ? "Yo'q" : `Ha`,
          isWanted: !n?.is_wanted ? "Yo'q" : `Ha`,
          ...n,
        };
        return item;
      }));

    teenagerInfo.value.personal_info &&
      (teenagerInfo.value.personal_info = {
        ...teenagerInfo.value.personal_info,
        birth_date:
          teenagerInfo.value.personal_info?.birth_date &&
          new Date(teenagerInfo.value.personal_info?.birth_date).toLocaleDateString('ru-Ru', {
            year: 'numeric',
            month: 'numeric',
            day: 'numeric',
          }),
        birth_region: teenagerInfo.value.personal_info?.birth_region?.name,
        birth_district: teenagerInfo.value.personal_info?.birth_district?.name,
        passport_type: teenagerInfo.value.personal_info?.passport_type?.name,
      });

    teenagerInfo.value.accept_center_info &&
      (teenagerInfo.value.accept_center_info = {
        ...teenagerInfo.value.accept_center_info,
        determined_location: teenagerInfo.value.accept_center_info.text,
        arrived_date:
          teenagerInfo.value?.arrived_date &&
          new Date(teenagerInfo.value?.arrived_date).toLocaleDateString('ru-Ru', {
            year: 'numeric',
            month: 'numeric',
            day: 'numeric',
          }),
        filled_date:
          teenagerInfo.value?.accept_center_info?.inspector?.filled_date &&
          new Date(teenagerInfo.value?.accept_center_info?.inspector?.filled_date).toLocaleDateString('ru-Ru', {
            year: 'numeric',
            month: 'numeric',
            day: 'numeric',
            hour: 'numeric',
            minute: 'numeric',
            second: 'numeric',
          }),
      });

    teenagerInfo.value.distribute_info &&
      (teenagerInfo.value.distribute_info = {
        ...teenagerInfo.value.distribute_info,
        distribution_type: teenagerInfo.value.distribute_info?.distribution_type?.text,
      });

    teenagerInfo.value.monitoring_info &&
      (teenagerInfo.value.monitoring_info = {
        ...teenagerInfo.value.monitoring_info,
        monitoring_status: teenagerInfo.value.monitoring_info?.monitoring_status?.text,
        school_type: teenagerInfo.value.monitoring_info?.school_type?.text,
        mastery: teenagerInfo.value.monitoring_info?.mastery?.text,
        character: teenagerInfo.value.monitoring_info?.character?.text,
      });

    teenagerInfo.value.employment_info &&
      (teenagerInfo.value.employment_info = {
        ...teenagerInfo.value.employment_info,
        employment_education_type: teenagerInfo.value.employment_info?.employment_education_type?.text,
      });

    structure.value = structure.value
      .map((n) => {
        return teenagerInfo.value[n.name] ? n : null;
      })
      .filter((n) => n);
  } catch (e) {
    alertThisError(e);
  }
  if (teenagerInfo.value) {
    if (teenagerInfo.value?.parent?.length) {
      teenParents.isNotEmpty = true;
      teenParents.father = teenagerInfo.value.parent.find((e) => {
        return e.parent_type === 'FATHER';
      });
      teenParents.mother = teenagerInfo.value.parent.find((e) => {
        return e.parent_type === 'MOTHER';
      });
    }
    loading.value = false;
  }
};

watch(
  () => route.params.id,
  async (id) => {
    if (id) {
      loading.value = true;
      await fetchTeenager();
      loading.value = false;
    }
  },
  {
    immediate: true,
  }
);

onMounted(async () => {
