<template>
  <div class="flex flex-col w-full">
    <template v-for="(n, i) in structure?.items" :key="i">
      <div v-if="findValue(n.value)" :key="i" class="info_wrapper_for_print">
        <span class="info_wrapper_label_for_print">
          {{ n.name }}
        </span>
        <p class="info_wrapper_text_for_print" v-html="n.ownFunct ? n.ownFunct(findValue(n.value)) : findValue(n.value)"></p>
      </div>
    </template>
    <component :is="rendererComponent" v-bind="structure?.props" :data="props_for_parents" />
  </div>
</template>

<script setup>
import {computed, defineProps} from 'vue';
const props = defineProps(['structure', 'data', 'props_for_parents']);

const rendererComponent = computed(() => props.structure?.component);

const findValue = (key) => {
  const itterbaleKey = key.split('.');
  if (itterbaleKey.length > 1) {
    let result = props.data[itterbaleKey[0]];
    for (let i = 1; i < itterbaleKey.length; i++) {
      result = result[itterbaleKey[i]];
    }
    if (result) {
      return result;
    } else {
      return null;
    }
  } else {
    if (key) {
      let result = props.data[key];
      if (result) {
        return result;
      } else {
        return null;
      }
    } else {
      return props.data;
    }
  }
  // const
};
</script>

